
.alert {
    display: inline-block;
}
.alert-wrapper {
    position: fixed;
    width: 100%;
    text-align: center;
    z-index: 99999;
}
.alert-wrapper.alert-bottom {
    bottom: 10%;
    top: auto;
}
.alert-wrapper.alert-top {
    top: 10%;
    bottom: auto;
}



