
@import url("color.css");
@import url("https://fonts.googleapis.com/css?family=Arimo");
html {
  position: relative;
  min-height: 100%;
}
body {
  font-family: 'Arimo', sans-serif;
  color: var(--grey);
}
a, .hash-tag {
  text-decoration: none !important;
  color: var(--color);
}
a:hover, .hash-tag:hover {
  color: var(--dark);
}
ul {
  list-style: none;
}
.color-dynamic {
  color: var(--color);
}
.btn, .pointer {
  cursor: pointer;
}
.btn:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
}
.btn-dynamic {
  background-color: var(--color);
  border-color: var(--color);
  color: #fff;
}
.btn-dynamic:hover {
  background-color: var(--dark);
  border-color: var(--dark);
}
.btn-outline-dynamic {
  color: var(--color);
  background-color: transparent;
  background-image: none;
  border-color: var(--color);
}
.btn-outline-dynamic:hover {
  color: #fff;
  background-color: var(--color);
}
.btn:disabled {
  cursor: no-drop;
}
.big-card-img-div {
  height: 283.07px;
  overflow: hidden;
  position: relative;
}
.big-card-img-div img {
  width: auto;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translateY(-50%) translateX(-50%);
  transform: translateY(-50%) translateX(-50%);
}
.input-group-addon {
  background-color: var(--grey);
  color: #fff;
}
.work-square img {
  width: 100%;
}
.work-square h4 {
  color: transparent;
}
.work-square .aligner {
  border-radius: 0rem;
}
.aligner {
  cursor: pointer;
  top: 0rem;
  left: 0rem;
  position: absolute;
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  border-radius: 0.25rem;
}
.aligner:hover {
  background: rgba(25, 25, 25, 0.5);
}
.aligner:hover h4 {
  color: #fff;
}
.tooltip {
  display: block !important;
  padding: 4px;
  z-index: 10000;
}
.tooltip .tooltip-inner {
    background: var(--grey);
    color: white;
    border-radius: 0.25rem;
    padding: 3px 8px;
    text-align: center;
}
.tooltip .tooltip-arrow {
    display: none;
}
.tooltip[aria-hidden='true'] {
    visibility: hidden;
    opacity: 0;
    -webkit-transition: opacity .15s, visibility .15s;
    transition: opacity .15s, visibility .15s;
}
.tooltip[aria-hidden='false'] {
    visibility: visible;
    opacity: 1;
    -webkit-transition: opacity .15s;
    transition: opacity .15s;
}
.tooltip-danger .tooltip-inner {
  background: var(--danger) !important;
}
.vue-typer {
  font-family: monospace;
}
.vue-typer .custom.char {
  color: #fff;
}
.vue-typer .custom.caret {
  width: 5px;
  background-color: #fff;
}

/* mixin for multiline */
.block-with-text {
  overflow: hidden;
  position: relative;
  line-height: 1.5em;
  max-height: 4.5em;
  text-align: justify;
  margin-right: -1em;
  padding-right: 1em;
}
.block-with-text:before {
    content: '...';
    position: absolute;
    right: 0;
    bottom: 0;
}
.block-with-text:after {
    content: '';
    position: absolute;
    right: 0;
    width: 1em;
    height: 1em;
    margin-top: 0.2em;
    background: white;
}



