
@import url("../../../styles/color.css");
.btn-top {
    color: white;
    opacity: .4;
    position: fixed;
    bottom: 11rem;
    right: 1.5rem;
    z-index: 1;
    width: 3rem;
    height: 3rem;
    background-color: var(--color);
    border-radius: 50%;
    padding: 0;
    margin: 0;
}
.below-menu {
    bottom: 5rem;
}
.btn-top:hover {
    opacity: 1;
}



