
@-webkit-keyframes v-gridStretchDelay
{
0%
    {
        -webkit-transform: scale(1);
                transform: scale(1);
}
50%
    {
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
        -webkit-opacity: 0.7;             
                opacity: 0.7;
}
100%
    {
        -webkit-transform: scale(1);
                transform: scale(1);
        -webkit-opacity: 1;             
                opacity: 1;
}
}
@keyframes v-gridStretchDelay
{
0%
    {
        -webkit-transform: scale(1);
                transform: scale(1);
}
50%
    {
        -webkit-transform: scale(0.5);
                transform: scale(0.5);
        -webkit-opacity: 0.7;             
                opacity: 0.7;
}
100%
    {
        -webkit-transform: scale(1);
                transform: scale(1);
        -webkit-opacity: 1;             
                opacity: 1;
}
}



